import React,{Component} from 'react'
import classes from './TodoList.module.css';
import TodoItem from './TodoItem/TodoItem'
// import PropTypes from 'prop-types';
import TodoListHeader from './TodoListHeader/TodoListHeader'
import TodoListActions from './TodoListActions/TodoListActions'

class TodoList extends Component{

  constructor(props){
    super(props);
    this.references = {
      todoItemInputBoxRef : React.createRef(),
      emptyTodoTitleSpanRef : React.createRef()
    }
  }

  getCheckedTodoItemsInTheList = () => {
    let todoListArray = this.props.todoListInfo.listDetails;
    const checkedTodos = todoListArray.reduce((result, current) => {
      if (current.isChecked) result.push(current.id);
      return result
    }, []);
    return checkedTodos;
  }

  deleteSelectedTodos = () => {
    const checkedTodos = this.getCheckedTodoItemsInTheList();
    for (let todoID of checkedTodos) {
      this.props.deleteTodoItemHandler(this.props.todoListId, todoID);
    }
  }

  marCompleteSelectedTodos = () => {
    const checkedTodos = this.getCheckedTodoItemsInTheList();
    for (let todoID of checkedTodos) {
      this.props.markCompleteSelectedTodoItemHandler(this.props.todoListId, todoID);
    }
  }

  handleAddNewTodoItemOnEnter = (event) => {
    if (event.which === 13) {
      this.handleAddNewTodoItem();
    }
  }

  handleAddNewTodoItem = () => {
    let {todoItemInputBoxRef,emptyTodoTitleSpanRef} = this.references;
    if (todoItemInputBoxRef.current.value.trim() === "") {
      emptyTodoTitleSpanRef.current.style.visibility = "visible";
      todoItemInputBoxRef.current.focus();
    } else {
      emptyTodoTitleSpanRef.current.style.visibility = "hidden";
      this.props.addNewTodoItemHandler(this.props.todoListId, todoItemInputBoxRef.current.value);
      todoItemInputBoxRef.current.value = "";
      todoItemInputBoxRef.current.focus();
    }
  }

  todoListCheckHandler = () => {
    this.props.toggleTodoListCheckedHandler(this.props.todoListId);
  }

  render(){
    let {todoListInfo} =this.props;
    let {listDetails,listTitle} = todoListInfo;

    let todoItemList = <div className={classes.emptyTodoList}>Empty Todo list</div>;
    if(listDetails.length !== 0){
      todoItemList = listDetails.map((todoItem) =>{
        return (
          <TodoItem key = {todoItem.id} 
                    todoTitle = {todoItem.title} 
                    todoCompleted = {todoItem.isCompleted}
                    todoItemId = {todoItem.id}
                    todoListID = {this.props.todoListId} />
          )
      });
    }
    return(
      <div className={classes.todoListWrapper}>
        <TodoListHeader todoListTitle={listTitle}
                        todoListCheckHandler={this.todoListCheckHandler}/>
        <TodoListActions ref={this.references}
                         handleAddNewTodoItemOnEnter = {this.handleAddNewTodoItemOnEnter}
                         handleAddNewTodoItem = {this.handleAddNewTodoItem}
                         deleteSelectedTodos = {this.deleteSelectedTodos}
                         marCompleteSelectedTodos = {this.marCompleteSelectedTodos}
        />
        <div className={classes.todoList}>
          {todoItemList}
        </div> 
      </div>
    );
  }
}

export default TodoList;