import React from 'react'
import AddNewTodoListModal from './AddNewTodoListModal/AddNewTodoListModal'
import DeleteTodoListModal from './DeleteTodoListModal/DeleteTodoListModal'
import PropType from 'prop-types'

const TodoDataModalBuilder = (props) => {
  let modalWrapper= null;
  if(props.showTodoListAddDataModal){
    modalWrapper = <AddNewTodoListModal
                      modalDisplayStateHandler={props.modalStateHandler}
                      addNewTodoList = {props.addNewTodoListHandler}
                      showModalBackDrop
                    />  
  }else if (props.showTodoListDeleteModal){
    modalWrapper = <DeleteTodoListModal
                      modalDisplayStateHandler={props.modalStateHandler}
                      deleteList={props.deleteTodoListHandler}
                      showModalBackDrop
                    />    
  }
  
  return (modalWrapper);
}

TodoDataModalBuilder.propTypes = {
  showTodoListAddDataModal: PropType.bool.isRequired,
  showTodoListDeleteModal: PropType.bool.isRequired,
  modalStateHandler: PropType.func,
  addNewTodoHandler: PropType.func,
  deleteTodoListHandler: PropType.func
}

export default TodoDataModalBuilder;