import React from 'react'
import classes from './TodoAppActionBar.module.css'
import {SHOW_TODO_LIST_ADD_DATA_MODAL,SHOW_TODO_LIST_DELETE_MODAL} from '../../constants/TodoListDataModalConstants'
import PropTypes from 'prop-types';
import Button from '../../ui/Button/Button'

const TodoAppActionBar = ({modalStateHandler}) => {    
  return(
    <div className={classes.todoListHeader}>
      <Button clicked={modalStateHandler} datatarget={SHOW_TODO_LIST_ADD_DATA_MODAL}>Add New Todo List</Button>
      <Button clicked={modalStateHandler} datatarget={SHOW_TODO_LIST_DELETE_MODAL}>Delete Lists</Button>
    </div>
  )
}
export default React.memo(TodoAppActionBar);

TodoAppActionBar.propTypes = {
  modalStateHandler: PropTypes.func.isRequired
}