import React,{Component} from 'react'
import Modal from '../../../ui/Modal/Modal';
import PropTypes from 'prop-types';

class AddNewTodoListModal extends Component {
  constructor(props){
    super(props);
    this.addList = React.createRef();
    this.titleError = React.createRef();
  }

  componentWillUnmount(){
    this.addList = null;
    this.titleError  = null;
  }
  
  validateAndAddNewList = () => {
    if (this.addList.current.value.trim() === "") {
      this.titleError.current.style.visibility = "visible";
      this.addList.current.focus();
    } else {
      this.props.addNewTodoList(this.addList.current.value);
    }
  }

  render(){
    const modalHeader = "Add New Todo List";
    const modalBody = (<React.Fragment>
                          <div>
                            <label>Enter Title For New Todo List</label>
                          </div>
                          <div>
                            <input type="text" ref={this.addList}/>
                          </div>
                          <span ref={this.titleError}>
                            Title can not be empty
                          </span>
                        </React.Fragment>);
    const modalFooter = <React.Fragment>
                            <button onClick={this.props.modalDisplayStateHandler}>Cancel</button>
                            <button onClick={this.validateAndAddNewList}>Add</button>
                        </React.Fragment>
    return (
        <Modal  modalHeader={modalHeader}
                modalBody={modalBody} 
                modalFooter={modalFooter}
                backDropClickHandler={this.props.modalDisplayStateHandler}
                showBackDrop 
                />
    );
  } 
}

AddNewTodoListModal.propTypes ={
  modalDisplayStateHandler: PropTypes.func.isRequired,
  addNew: PropTypes.func.isRequired
}


export default AddNewTodoListModal;