import React, {Component} from 'react'
import TodoList2 from './TodoList/TodoList'
import classes from './TodoLists.module.css'
import PropTypes from 'prop-types'

class TodoLists extends Component {

  render (){
    let todoListsContainer = "";
    let {todoLists} = this.props;
    if(!todoLists ||todoLists.length === 0 ){
      todoListsContainer = (<p className={classes.emptyTodoContainer}>No Todo Lists To display</p>);
    } else {
        todoListsContainer = todoLists.map((todoList) => {
          return (
              <TodoList2 key={todoList.listId} 
                         todoListInfo ={todoList}
                         todoListId={todoList.listId}
                         toggleTodoListCheckedHandler = {this.props.toggleTodoListCheckedHandler}
                         addNewTodoItemHandler={this.props.addNewTodoItemHandler}
                         markCompleteSelectedTodoItemHandler ={this.props.markCompleteSelectedTodoItemHandler}  
                         deleteTodoItemHandler = {this.props.deleteTodoItemHandler}
                         />
          )
        });
      }
    return (todoListsContainer);
  }  
}

TodoLists.propTypes ={
  addNewTodoItemHandler: PropTypes.func,
  todoLists: PropTypes.arrayOf(PropTypes.object),
  deleteTodoHandler: PropTypes.func,
  markCompleteHandler: PropTypes.func,
  toggleTodoListCheckedHandler: PropTypes.func
}

TodoLists.defaultProps ={
  todoLists: []
}

export default TodoLists;