import React , {Component} from 'react'
import classes from './TodoListsBuilder.module.css'
import TodoLists2 from '../../components/TodoLists/TodoLists'
import addIcon from '../../assets/add_todo_list.png'
import TodoAppActionBar from '../../components/TodoAppActionBar/TodoAppActionBar'
import TodoItemActionsContext from '../../context/TodoItemActionsContext'
import TodoListDataModalBuilder from '../../components/TodoListDataModal/TodoListDataModalBuilder'
import {SHOW_TODO_LIST_ADD_DATA_MODAL} from '../../constants/TodoListDataModalConstants'

class TodoListsBuilder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      todoLists: [],
      showTodoListAddDataModal: false,
      showTodoListDeleteModal: false,
    }
  }

  toggleModalDisplay = (e) => {
    let modalName= e.target.getAttribute('datatarget');
    const updatedState = {
      ...this.state
    }
    
    if(modalName){
      updatedState[[modalName]] = !updatedState[[modalName]];
      this.setState({
        [modalName]: updatedState[[modalName]]
      });
    }else{
      let modalToToggleOnBackdropClick= null;
      for(let key of Object.keys(updatedState)){
        if(updatedState[key] === true) {
          modalToToggleOnBackdropClick=key;
          break;
        }
      }
      updatedState[[modalToToggleOnBackdropClick]] = ! updatedState[modalToToggleOnBackdropClick];
      this.setState(() => {
          return{
            [modalToToggleOnBackdropClick]: updatedState[modalToToggleOnBackdropClick]
          }    
      });
    }
  }

  addNewTodoListHandler = (title) => {
    debugger;
    const stateObject = {
      ...this.state
    }
    stateObject.todoLists.push({
      listId: Date.now().toString(),
      listDetails: [],
      listTitle: title,
      isSelected: false
    });
    this.setState({
      todoLists: stateObject.todoLists,
      showTodoListAddDataModal: false,
    });
  }

  addNewTodoItemHandler = (listId, title) => {
    const newTodoItem = {title: title,id:Date.now().toString(),isCompleted:false,isChecked:false}
    const stateObject = {
      ...this.state
    }
    const todoList = stateObject.todoLists.reduce((result, current) => {
      result = current.listId === listId ? result[result.length] = current : result;
      return result
    }, []);
    todoList.listDetails.unshift(newTodoItem);
    this.setState({
      todoLists: stateObject.todoLists
    })
  }

  getCheckedTodoItems = (stateObject, listId, itemId) => {
    const todoList = stateObject.todoLists.reduce((result, current) => {
      if (current.listId === listId) result.push(current);
      return result;
    }, [])[0];
    let todoItem = [];
    if (itemId && itemId !== null) {
      todoItem = todoList.listDetails.reduce((result, current) => {
        if (current.id === itemId) result.push(current);
        return result;
      }, [])[0];
    }
    return [todoItem, todoList];
  }

  toggleTodoItemMarkCompleteHandler = (listId, itemId) => {
    const stateObject = {
      ...this.state
    }
    const [todoItem, ] = this.getCheckedTodoItems(stateObject, listId, itemId);
    todoItem.isCompleted = !todoItem.isCompleted;
    this.setState({
      todoLists: stateObject.todoLists
    });
  }

  markCompleteSelectedTodoItemHandler = (listId, itemId) => {
    const stateObject = {
      ...this.state
    }
    const [todoItem, ] = this.getCheckedTodoItems(stateObject, listId, itemId);
    todoItem.isCompleted = true;
    this.setState({
      todoLists: stateObject.todoLists
    });
  }

  toggleTodoItemCheckedHandler = (listId, itemId) => {
    const stateObject = {
      ...this.state
    }
    const [todoItem, ] = this.getCheckedTodoItems(stateObject, listId, itemId);
    todoItem.isChecked = !todoItem.isChecked;
    this.setState({
      todoLists: stateObject.todoLists
    });
  }

  deleteTodoItemHandler = (listId, itemId) => {
    const stateObject = {
      ...this.state
    }
    const [todoItem, todoList] = this.getCheckedTodoItems(stateObject, listId, itemId);
    const deleteIndex = todoList.listDetails.findIndex((cur) => cur.id === todoItem.id);
    todoList.listDetails.splice(deleteIndex, 1);
    this.setState({
      todoLists: stateObject.todoLists
    });
  }

  toggleTodoListCheckedHandler = (listId) => {
    debugger;
    const stateObject = {
      ...this.state
    }
    const [, todoList] = this.getCheckedTodoItems(stateObject, listId);
    todoList.isSelected = !todoList.isSelected;
    this.setState({
      todoLists: stateObject.todoLists
    });
  }

  deleteTodoListHandler = () => {
    const stateObject = {
      ...this.state
    }
    const listIdsToDelete = stateObject.todoLists.reduce((result, cur) => {
      if (cur.isSelected) result.push(cur.listId);
      return result;
    }, []);
    listIdsToDelete.forEach(element => {
      const deleteIndex = stateObject.todoLists.findIndex((cur) => cur.listId === element);
      stateObject.todoLists.splice(deleteIndex, 1);
    });

    this.setState({
      todoLists: stateObject.todoLists,
      showTodoListDeleteModal: false,
    });
  }
    

  render(){
    let addListButton = null;
    if(this.state.todoLists.length !== 0){
      addListButton = (
        <div>
          <button className = {classes.addIcon} onClick={this.toggleModalDisplay} datatarget={SHOW_TODO_LIST_ADD_DATA_MODAL}>
            <img src = {addIcon} alt = "add todo list"></img>
          </button>
        </div>
      );
    }
    return(
      <React.Fragment>
        <TodoAppActionBar modalStateHandler={this.toggleModalDisplay} />
        <TodoListDataModalBuilder showTodoListAddDataModal={this.state.showTodoListAddDataModal} 
                                  modalStateHandler={this.toggleModalDisplay}
                                  showTodoListDeleteModal={this.state.showTodoListDeleteModal}
                                  addNewTodoListHandler={this.addNewTodoListHandler}  
                                  deleteTodoListHandler={this.deleteTodoListHandler}
                                />
        <div className={classes.todoListsContainer}>
            <TodoItemActionsContext.Provider value={{ toggleTodoItemCheckedHandler: this.toggleTodoItemCheckedHandler,
                                                    toggleTodoItemMarkCompleteHandler: this.toggleTodoItemMarkCompleteHandler ,
                                                    deleteTodoItemHandler: this.deleteTodoItemHandler ,
                                                    toggleTodoListCheckedHandler : this.toggleTodoListCheckedHandler
            }} >
            <TodoLists2 todoLists={this.state.todoLists}
                        addNewTodoItemHandler= {this.addNewTodoItemHandler}
                        markCompleteSelectedTodoItemHandler={this.markCompleteSelectedTodoItemHandler }
                        deleteTodoItemHandler={this.deleteTodoItemHandler}
                        toggleTodoListCheckedHandler ={this.toggleTodoListCheckedHandler} />
          </TodoItemActionsContext.Provider>
            {addListButton}
        </div>
      </React.Fragment>  
    );
  }
}

export default TodoListsBuilder;
