import React,{Component} from 'react'
import Modal from '../../../ui/Modal/Modal'
import PropTypes from 'prop-types';

class DeleteTodoListModal extends Component{
  render(){
    const modalHeader = <React.Fragment>Delete Todo List</React.Fragment>;
    const modalBody = ( <label>
                          Are you sure you want to delete selected Todo Lists ?!!
                        </label>);
    const modalFooter = <React.Fragment>
                            <button onClick={this.props.modalDisplayStateHandler}>Cancel</button>
                            <button onClick={this.props.deleteList}>Delete</button>
                        </React.Fragment>
    return (
      <Modal  modalHeader={modalHeader}
              modalBody={modalBody} 
              modalFooter={modalFooter}
              backDropClickHandler={this.props.modalDisplayStateHandler}
              showBackDrop
    />);
  }
}

DeleteTodoListModal.propTypes ={
  modalDisplayStateHandler: PropTypes.func.isRequired,
  deleteList: PropTypes.func.isRequired
}
export default DeleteTodoListModal;