
import React from 'react'
import classes from './TodoListActions.module.css';

const TodoListActions = React.forwardRef((props,ref) => {
  debugger;
  const { todoItemInputBoxRef, emptyTodoTitleSpanRef } = ref;
  return (
    <React.Fragment>
      <div>
        <input placeholder="Add Your Todo Task" ref={todoItemInputBoxRef}
              onKeyPress={props.handleAddNewTodoItemOnEnter}/>
        <button className={classes.addTodoTaskBtn} 
                onClick={props.handleAddNewTodoItem}>
                    Add Todo
        </button>
      </div>
      <span ref={emptyTodoTitleSpanRef}>
        <em>Task can not be empty</em>
      </span>
      <div className={classes.todoListActionBar}>
        <button onClick={props.deleteSelectedTodos}>
            Delete Todo
        </button>
        <button onClick={props.marCompleteSelectedTodos}>
            Mark Complete
        </button>  
      </div>
    </React.Fragment> 
  )
});




export default TodoListActions;














