import React from 'react'
import PropTypes from 'prop-types'

const Button = (props) => {
  return (
    <Button onClick={props.clicked} datatarget={props.datatarget}>{props.children}</Button>
  );
}

Button.propTypes ={
  clicked: PropTypes.func.isRequired,
  datatarget:PropTypes.string
}

Button.defaultProps ={
  datatarget:null
}

export default Button;